.sider {
  // width: 30%;
  width: 320px;
  height: 100%;
  border-left: 1px solid #d9d9d9;
  display: flex;
  flex-direction: column;
  :global {
    .ant-tabs {
      flex: 1;
    }
    .ant-tabs-tabpane {
      & > div {
        font-size: 12px;
        line-height: 1.2;
        padding: 10px 20px;
        overflow: auto;
        pre {
          white-space: break-spaces;
        }
      }
    }
  }
}
