.card {
  font-size: 10px;
  padding: 4px;
  border-radius: 4px;
  width: 160px;

  .header {
    color: #fff;
    text-align: center;
    padding-bottom: 2px;
    display: flex;
    justify-content: space-between;
    :first-child {
      flex: 1;
    }
  }

  .panel {
    background-color: #fff;
    border-radius: 4px;
    padding: 4px;

    .title {
      font-size: 8px;
      display: flex;
      justify-content: space-between;
    }
  }
}
